@use "variables" as v;
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: v.$main_font;
  font-size: 1.6rem;

  &.fix-body {
    overflow: hidden;
  }

  &.body-scroll {
    padding-top: 5.8rem;
  }
}

p {
  font-size: 2rem;
  color: v.$black;
  line-height: 2.4rem;
}

.container {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: v.$black;
}

h1,
h2,
h3 {
  margin: 0 0 3rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}
