html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fix-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 5.8rem;
}

p {
  font-size: 2rem;
  color: #000a0c;
  line-height: 2.4rem;
}

.container {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000a0c;
}

h1,
h2,
h3 {
  margin: 0 0 3rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #047a8f;
}
@media (min-width: 768px) {
  .header.fixed {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
  }
}
.header .content-header {
  padding: 0.6rem;
}
@media (min-width: 768px) {
  .header .content-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header h1 a {
  color: #f1f7f8;
  text-align: center;
}

@media (min-width: 768px) {
  .nav {
    display: flex;
    gap: 2rem;
  }
}
.nav a {
  display: block;
  text-align: center;
  text-transform: uppercase;
  color: #f1f7f8;
  margin-bottom: 1rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .nav a {
    margin: 0;
  }
}
.nav a:hover {
  color: #a89f18;
}

.video {
  position: relative;
  max-height: 60rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(141, 58, 180);
  background: linear-gradient(90deg, rgba(141, 58, 180, 0.3491596468) 0%, rgba(151, 29, 253, 0.3211484423) 50%, rgba(29, 58, 253, 0.3099439605) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.content-video h2 {
  color: #f1f7f8;
  font-size: 6rem;
  margin: 0;
}
.content-video p {
  background-color: #610088;
  display: inline-block;
  padding: 1rem;
  color: #f1f7f8;
  border-radius: 3px;
  font-weight: 700;
}

.festival {
  padding: 6rem 0;
}
@media (min-width: 768px) {
  .festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.content-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .content-festival {
    margin-top: 0;
  }
}
.content-festival h2 {
  margin-bottom: 1rem;
}
.content-festival .date {
  color: #610088;
  font-weight: 700;
  margin-bottom: 0;
}

.lineup {
  background-color: #610088;
  padding: 5rem 0;
}
.lineup .day {
  margin: 2rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #f1f7f8;
}

@media (min-width: 768px) {
  .stage-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.stage-container p {
  color: #000a0c;
}

.stage {
  padding-top: 2rem;
}
.stage .stage-name::after {
  content: "";
  display: block;
  width: 15rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.indie .stage-name:after {
  background-image: url(../img/icono_edm.png);
}

.rock .stage-name:after {
  background-image: url(../img/icono_rock.png);
}

.bg-yellow {
  background-color: #a89f18;
}
.bg-yellow li:nth-child(odd) {
  background-color: #968e15;
}

.bg-green {
  background-color: #047a8f;
}
.bg-green li:nth-child(odd) {
  background-color: #03697b;
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2rem;
  font-weight: 700;
}
.calendar span {
  font-weight: 400;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.6rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.6rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.6rem;
  }
}
@media (min-width: 992px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1.6rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}
.overlay picture {
  display: block;
  max-width: 65rem;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #f1f7f8;
}
@media (min-width: 768px) {
  .tickets .content-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.pass .pass-name {
  text-align: center;
  color: #f1f7f8;
  font-size: 2.2rem;
}
.pass-head {
  background-color: #610088;
}
.pass-head p {
  text-transform: uppercase;
  font-size: 1.5rem;
}
.pass-body {
  background-color: #f1f7f8;
}
.pass-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: #f1f7f8;
  padding: 1.2rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass-footer {
  background-color: #35048f;
}
.pass-footer p {
  font-size: 2rem;
}
.pass-head, .pass-footer {
  padding: 0.5rem;
}
.pass-head p, .pass-footer p {
  text-align: center;
  color: #f1f7f8;
  margin: 0;
  font-weight: 700;
}

.footer {
  background-color: #610088;
  padding: 1rem;
}
.footer p {
  text-align: center;
  font-weight: 700;
  color: #f1f7f8;
  margin: 0;
}